class Navbar extends HTMLElement {
  constructor () {
    super ();
  }

  connectedCallback () {
    this.render ();
  }

  render () {
    this.innerHTML = `
        <nav>
            <div class="navbar-img">
                <img src="" alt="logo" class="img">
            </div>
            <ul>
                <li><a href="#">Home</a></li>
                <li><a href="#">About</a></li>
                <li><a href="#/login">Admin Login</a></li>
            </ul>
        </nav>
        `;
  }
}

customElements.define ('navbar-template', Navbar);
